.items{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
}
@media (max-width: 1000px){
    .items{
        grid-template-columns: 1fr 1fr 1fr;
    }
}
@media (max-width: 700px){
    .items{
        grid-template-columns: 1fr 1fr;
    }
}
.pages{
    display: inline-block;
    max-width: 80%;
    /*align-items: center;*/
}
.flex{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
